import React from "react";
import { Link } from "react-router-dom";

const Header = () => (
  <header className="bg-dark text-light py-3 px-4 d-flex justify-content-between align-items-center">
    <h1 className="fs-3">UPLEVEL 3D</h1>
    <h3 className="mt-3 mb-3"> Aqui sua Imaginação vira Realidade!!!</h3>
    <nav className="d-flex gap-3">
      <Link className="text-light text-decoration-none" to="/">Home</Link>
      <Link className="text-light text-decoration-none" to="/figures">Figures</Link>
      <Link className="text-light text-decoration-none" to="/decoracao">Decoração</Link>
      <Link className="text-light text-decoration-none" to="/contato">Contato</Link>
      <a className="text-light text-decoration-none" href="https://instagram.com/uplevel3d" target="_blank" rel="noreferrer">Instagram</a>
      <a className="text-light text-decoration-none" href="https://youtube.com/@uplevel3d" target="_blank" rel="noreferrer">YouTube</a>
    </nav>
  </header>
);

export default Header;
